<template>
  <div class="sort">
    <my-nav></my-nav>
    <my-head :mes='title'></my-head>
    <div class="select">
      <p v-for='(item,index) in sortArr' ref='kind' @click='switchFn(index)'>{{item.name}}</p>
    </div>
    <my-list :lists='wawa'></my-list>
    <!-- <infinite-loading @infinite="infiniteHandler" >
        <span slot="no-more" ref='no_more'>亲~已经到底啦...</span>
    </infinite-loading> -->
  </div>
</template>

<script>
import nav from '../nav.vue'
import head from '../head.vue'
import {mapGetters,mapActions} from 'vuex'
import goodlist from '../goodlist.vue'
import InfiniteLoading from 'vue-infinite-loading'
import { Indicator } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
        title:{'center':'分类'},
        sortArr:[
        {'name':'新鲜蔬果','class':'fruit'},
        {'name':'休闲食品','class':'snack'},
        {'name':'米谷粮油','class':'crop'},
        {'name':'酒水饮料','class':'drink'},
        {'name':'清洁纸品','class':'clean'},
        {'name':'全部商品','class':''},      
        ],
        list:[],
        wawa:[]
        // goodList:''
    }
  },
  computed:mapGetters({
    tag:'getChosenSort',
    goods:'getGoodList',
    selected:'getSelected'
  }),
  methods:{
    ...mapActions([
        'setChosenSort',
        'setGoodList',
        'setSelected'
    ]),
    switchFn:function(index){
        this.setSelected('')  
        for(var i=0;i<this.$refs.kind.length;i++){
            if(i==index){
                this.$refs.kind[i].style.backgroundColor='#C5BFAC'
                this.setChosenSort(i)
            }else{
                this.$refs.kind[i].style.backgroundColor='transparent'
            }
        }
        if(index==5){
            this.setSelected(this.goods)
        }else{
            var temp=[],cla=this.sortArr[index].class
            for(var i=0;i<this.goods.length;i++){
                if(this.goods[i].class==cla){
                    temp.push(this.goods[i])
                }
            }
            this.setSelected(temp)
            this.wawa=this.selected
        }     
    },
    // infiniteHandler($state) {        
    //     if(this.list.length==this.goodList.length&&this.list.length!=0){
    //         $state.complete();
    //         this.goodList=[]
    //         this.$refs.no_more.style.fontSize='25px';
    //     }else{
    //         setTimeout(() => {
    //             var temp = [];
    //             for (let i = this.list.length; i<=this.list.length + 5&&i<this.goodList.length; i++) {
    //               temp.push(this.goodList[i]);
    //             }
    //             this.list = this.list.concat(temp);
    //             $state.loaded();
    //           }, 500);
    //     }        
    // },   
  },
  components:{
  	'my-nav':nav,
    'my-head':head,
    'my-list':goodlist,
    InfiniteLoading
  },
  mounted:function(){
    this.$refs.kind[this.tag].style.backgroundColor='#C5BFAC'
    this.setGoodList()
    var that=this
    Indicator.open('加载中...');
    setTimeout(function(){
            if(that.tag==5){
                that.setSelected(that.goods)
        }else{
            var temp=[],cla=that.sortArr[that.tag].class
            for(var i=0;i<that.goods.length;i++){
                if(that.goods[i].class==cla){
                    temp.push(that.goods[i])
                }
            }
            that.setSelected(temp)

        }
        that.wawa=that.selected
        Indicator.close();
    },1000)
   } 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.sort
    padding-bottom pxtorem(150px)
    padding-top pxtorem(100px)
    .select
        width pxtorem(660px)
        padding pxtorem(25px) pxtorem(45px)
        height pxtorem(150px)
        background-color #f2f2f2
        overflow hidden
        p
            float left
            height pxtorem(75px)
            width pxtorem(220px)
            line-height pxtorem(75px)
            text-align center
            font-size pxtorem(25px)
            border-radius pxtorem(10px)
            color #292929
  
</style>



