import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import homepage from '@/components/home/homepage'
import sortpage from '@/components/sort/sortpage'
import carpage from '@/components/car/carpage'
import mypage from '@/components/mine/mypage'
import citylist from '@/components/home/citylist'
import detail from '@/components/detail'
import search from '@/components/home/search'
import coupon from '@/components/home/coupon'
import storage from  '@/components/mine/storage'
import address from '@/components/mine/address'
import addAddress from '@/components/mine/addAddress'
import order from '@/components/order'
import selectAdd from '@/components/selectAdd'
import log from '@/components/log/log'
import register from '@/components/log/register'
import forget from '@/components/log/forget'
import information from '@/components/mine/information'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name:'homepage',     
      component: homepage
    },
    {
      path: '/sort',
      name:'sortpage',     
      component: sortpage
    },
    {
      path: '/car',
      name:'carpage',     
      component: carpage
    },
    {
      path: '/mine',
      name:'mypage',     
      component: mypage
    },
    {
      path: '/citylist',
      name:'citylist',     
      component: citylist
    },
    {
      path: '/detail',
      name:'detail',     
      component: detail
    },
    {
      path: '/search',
      name:'search',     
      component: search
    },
    {
      path: '/coupon',
      name:'coupon',     
      component: coupon
    },
    {
      path: '/storage',
      name:'storage',     
      component: storage
    },
    {
      path: '/address',
      name:'address',     
      component: address
    },
    {
      path: '/addAddress',
      name:'addAddress',     
      component: addAddress
    },
    {
      path: '/order',
      name:'order',     
      component: order
    },
    {
      path:'/selectAdd',
      name:'selectAdd',
      component:selectAdd
    },
    {
      path:'/log',
      name:'log',
      component:log
    },
    {
      path:'/register',
      name:'register',
      component:register
    },
    {
      path:'/forget',
      name:'forget',
      component:forget
    },
    {
      path:'/information',
      name:'information',
      component:information
    },
  ]
})



