<template>
  <div class="log">
    <my-head :mes='title'></my-head>
		<input type="text" ref='user'>
		<input type="password" ref='pwd'>
		<p>
    <span @click='forgetFn'>忘记密码</span>
    <span @click='registerFn'>立即注册</span>  
    </p>
    <input type="button" value="登 录" @click='logFn'>
  </div>
</template>

<script>
import head from '../head.vue'
import { Toast } from 'mint-ui';
import {mapActions} from 'vuex'
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../../assets/img/back.png'),'center':'登录'}
    }
  },
  components:{
  	'my-head':head
  },
  methods:{
    ...mapActions([
      'setStates'
    ]),
  	forgetFn:function(){
      this.$router.push('/forget')
    },
    registerFn:function(){
      this.$router.push('/register')
    },
    logFn:function(){
      if(this.$refs.user.value==''||this.$refs.pwd.value==''){
          Toast({
              message: '用户名或密码不能为空',
              duration:800
            });
      }else{
          if(this.$refs.user.value=='888'&&this.$refs.pwd.value=='888'){
                this.setStates('888')
                this.$router.go(-1)
            }else{
              Toast({
                message: '用户名或密码错误',
                duration:800
              });
            }
        }
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.log
  padding-top pxtorem(100px)
  width pxtorem(750px) 
  height pxtorem(1234px)
  background-color #f2f2f2
  >input
    display block
    margin auto
    width pxtorem(650px)
    height pxtorem(100px)
    margin-top pxtorem(30px)
    border-radius pxtorem(15px)
    border pxtorem(1px) solid #ccc
    outline none
    background white url(../../assets/img/user.png) no-repeat pxtorem(30px) pxtorem(30px)
    background-size pxtorem(45px)
    font-size pxtorem(35px)
    text-indent pxtorem(80px)
  >input:nth-of-type(2)
    background-image url(../../assets/img/pwd.png)
    background-size pxtorem(40px)
  >p
    width pxtorem(650px)
    margin auto
    overflow hidden
    margin-top pxtorem(10px)
    >span
      float left
      font-size pxtorem(27px)
      color #666
    >span:nth-of-type(2)
      float right
  >input[type=button]
    color white
    background-color #292929
    font-size pxtorem(33px)
    word-spacing pxtorem(30px)
    background-image none
    text-indent 0
    
	
</style>



