<template>
  <div class="coupon">
    <my-head :mes='title'></my-head>
	<div class="inner">
		<div>
			<img src="../../assets/img/mall.png" alt="">
			<span>生活购</span>
		</div>
		<div>
			<img :src="coupon.url" alt="">
			<input type="button" value="立即领取">
			<p>使用条件：</p>
			<span> 满{{coupon.full}}.00元，减{{coupon.off}}.00元</span>
			<p>可用时间：</p>
			<span> 2017-10-01至2017-10-07
			00:00-23:59
			</span>
		</div>
		<div>
			<p>店铺地址：</p>
			<span> 南山区</span>
			<p>联系电话：</p>
			<span> 18278051570</span>
			<p>返回首页</p>
		</div>
	</div>
  </div>
</template>

<script>
import head from '../head.vue'
import {mapGetters} from 'vuex'
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../../assets/img/back.png')}
    }
  },
  components:{
  	'my-head':head
  },
  computed:mapGetters({
  	coupon:'getSelectedCoupon'
  }),
  mounted:function(){

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.coupon
	width pxtorem(750px)
	height pxtorem(1234px)
	background-color #ED213A
	padding-top pxtorem(100px)
	.inner
		width pxtorem(650px)
		height pxtorem(1000px)
		background-color white
		margin auto
		margin-top pxtorem(50px)
		box-shadow pxtorem(10px) pxtorem(10px) pxtorem(10px) pxtorem(1px) black
		border-radius pxtorem(10px)
		font-family '宋体'
		>div:nth-of-type(1)
			height pxtorem(100px)
			border-bottom pxtorem(3px) dashed #ccc
			line-height pxtorem(100px)
			>img
				width pxtorem(50px)
				display inline-block
				margin pxtorem(25px)
				margin-right 0
				vertical-align middle
			>span
				font-size pxtorem(28px)
				vertical-align middle
				color gray
		>div:nth-of-type(2)
			width pxtorem(650px)
			height pxtorem(550px)
			border-bottom pxtorem(3px) dashed #ccc
			overflow hidden
			>img
				width pxtorem(390px)
				height pxtorem(172px)
				display block
				margin pxtorem(30px) 0 pxtorem(30px) pxtorem(130px)
			>input[type=button]
				display block
				width pxtorem(233px)
				height pxtorem(65px)
				background-color #FD445B
				font-size pxtorem(30px)
				color white
				border-radius pxtorem(10px)
				outline none
				margin auto
				margin-bottom pxtorem(30px)
			>p
				padding pxtorem(20px) pxtorem(0px)
				width pxtorem(130px)
				margin-left pxtorem(77px)
				font-size pxtorem(25px)
				height pxtorem(35px)
				color #292929
				float left
			>span
				float left
				display block
				width pxtorem(400px)
				padding pxtorem(20px) pxtorem(0px)
				height pxtorem(35px)
				color gray
		div:nth-of-type(3)
			// padding pxtorem(20px) pxtorem(77px)
			>p
				padding pxtorem(20px) pxtorem(0px)
				width pxtorem(130px)
				margin-left pxtorem(77px)
				font-size pxtorem(25px)
				height pxtorem(35px)
				border-bottom pxtorem(1px) solid #ccc
				color #292929
				float left
			>span
				float left
				display block
				width pxtorem(400px)
				padding pxtorem(20px) pxtorem(0px)
				height pxtorem(35px)
				border-bottom pxtorem(1px) solid #ccc
				color gray
				
			
				
</style>



