<template>
  <div class="order">
    <my-head :mes='title'></my-head>
    <div @click='selectAddressFn'>
    	<p>
	    	<span>{{selectedAdd.name}}</span>
	    	<span>{{selectedAdd.phone}}</span>
    	</p>
    	<p>{{selectedAdd.address}}</p>
    	<img src="../assets/img/right.png" alt="">
    </div>
    <div class="blank"></div>
    <p class="item">购买商品</p>
    <ul>
    	<li v-for='item in buy'>
    		<img :src="item[0].des" alt="">
    		<div>
    			<p>{{item[0].name}}</p>
    			<p>￥{{item[0].price}}
    				<span>x {{item[1]}}</span>
    			</p>
    		</div>
    	</li>
    </ul>
    <p class="item">运费：<span>￥10</span></p>
    <p class="item">备注：</p>
    <div class="blank"></div>
    <p class="item">支付方式</p>
    <div v-for='(item,index) in pay' class="payment" @click='choosePayFn(index)'>
    	<img :src="item.url" alt="">
    	<span>{{item.name}}</span>
    	<div ref='options'><img src="../assets/img/checked.png" alt=""></div>
    </div>
    <div>
    	<span>合计：</span>
    	<span>￥{{all}}</span>
    	<button>立即支付</button>
    </div>
  </div>
</template>

<script>
import head from './head.vue'
import {mapActions,mapGetters} from 'vuex'
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../assets/img/back.png'),'center':''},
      buy:'[]',
      all:0,
      pay:[{'name':'支付宝','url':require('../assets/img/alipay.jpg')},
      		{'name':'微信支付','url':require('../assets/img/wechat.jpg')},]
    }
  },
  computed:mapGetters({
  	unpaid:'getUnpaid',
  	getAdd:'getAddress',
  	selectedAdd:'getSelectedAdd'
  }),
  methods:{
  	...mapActions([
  		'setSelectedAdd'
  	]),
  	choosePayFn:function(index){
  		for(var i=0;i<this.pay.length;i++){
  			if(i==index){
  				this.$refs.options[i].style.backgroundColor='orange';
  			}else{
  				this.$refs.options[i].style.backgroundColor='#ccc';
  			}
  		}
  	},
  	selectAddressFn:function(){
  		this.$router.push('/selectAdd')
  	}
  },
  components:{
  	'my-head':head
  },
  mounted:function(){
	if(this.selectedAdd==''){
		for(var i=0;i<this.getAdd.length;i++){
			if(this.getAdd[i].def){
				this.setSelectedAdd(this.getAdd[i])
			}
		}
	}
	this.buy=this.unpaid[this.unpaid.length-1][1];
	var temp=0;
	for(var i=0;i<this.buy.length;i++){
		temp+=this.buy[i][0].price*this.buy[i][1]
	}
	this.all=temp+10;
 }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../assets/common.styl'
.order
	padding-top pxtorem(100px)
	padding-bottom pxtorem(100px)
	div:nth-of-type(2)
		width pxtorem(710px)
		height pxtorem(102px)
		padding pxtorem(32px) pxtorem(20px)
		border-bottom pxtorem(1px) solid gray
		position relative 
		p:nth-of-type(1)
			overflow hidden
			span
				float left
				width pxtorem(200px)
				font-size pxtorem(30px)
				color gray
		p:nth-of-type(2)
			margin-top pxtorem(30px)
			font-size pxtorem(30px)
			color gray
		>img
			position absolute
			width pxtorem(20px)
			right pxtorem(20px)
			top pxtorem(60px)
	.blank
		height pxtorem(20px)
		background-color #f2f2f2
	.item
		width pxtorem(730px)
		padding-right pxtorem(20px)
		height pxtorem(85px)
		border-bottom pxtorem(1px) solid #ccc
		line-height pxtorem(85px)
		text-indent pxtorem(20px)
		color #666
		font-size pxtorem(30px)
		overflow hidden
		span
			float right
	>p:nth-of-type(1)
		border-top pxtorem(1px) solid #ccc
	ul
		li
			width pxtorem(750px)
			height pxtorem(190px)
			border-bottom pxtorem(1px) solid #ccc
			overflow hidden
			>img
				width pxtorem(150px)
				height pxtorem(150px)
				margin pxtorem(20px)
				display block
				float left
			>div
				width pxtorem(560px)
				height pxtorem(150px)
				padding pxtorem(20px) 0
				float left
				p:nth-of-type(1)
					font-size pxtorem(30px)
					color #292929
				p:nth-of-type(2)
					font-size pxtorem(35px)
					height pxtorem(35px)
					color orange
					margin-top pxtorem(30px)
					overflow hidden
					padding-right pxtorem(20px)
					span
						float right
						font-size pxtorem(30px)
						color #666
	.payment
		width pxtorem(750px)
		height pxtorem(90px)
		border-bottom pxtorem(1px) solid #ccc
		overflow hidden
		line-height pxtorem(90px)
		>img
			width pxtorem(60px)
			height pxtorem(60px)
			margin pxtorem(15px) pxtorem(20px)
			display block
			float left
		>span
			font-size pxtorem(30px)
			color #666
			float left
		>div
			width pxtorem(40px)
			height pxtorem(40px)
			float right
			background-color #ccc
			border-radius 50%
			text-align center
			line-height pxtorem(40px)
			margin pxtorem(30px) pxtorem(20px)
			margin-right pxtorem(60px)
			img
				width pxtorem(30px)
	>div:last-child
		width pxtorem(710px)
		height pxtorem(59px)
		border-top pxtorem(1px) solid #ccc
		line-height pxtorem(59px)
		padding pxtorem(20px)
		font-size pxtorem(35px)
		color gray
		position fixed
		bottom 0
		left 0
		span:nth-of-type(2)
			color orange
		button
			width pxtorem(250px)
			height pxtorem(59px)
			display inline-block
			margin-left pxtorem(200px)
			vertical-align top
			background-color #ee4147
			border none
			color white
			font-size pxtorem(30px)
</style>



