<template>
  <div class="add">
    <my-head :mes='title'></my-head>
	<input type="text" placeholder='收货人姓名' ref='name'>
	<input type="text" placeholder='联系电话' ref='phone'>
	<textarea placeholder='请输入地址...' ref='address'></textarea>
	<div @click='saveFn'>保存地址</div>
	
  </div>
</template>

<script>
import head from '../head.vue'
import {mapActions} from 'vuex'
import { Toast } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../../assets/img/back.png')},
      toasts:[]
    }
  },
  methods:{
  	...mapActions([
  		'setAddress'
  	]),
  	saveFn:function(){
  		var bol1,bol2,bol3,reg1=/^[\u4e00-\u9fa5]{2,}$/,reg2=/^[1][358][0-9]{9}$/,reg3=/^^[\u4e00-\u9fa5]{10,}$/
  		bol1=reg1.test(this.$refs.name.value)
  		bol2=reg2.test(this.$refs.phone.value)
  		bol3=reg3.test(this.$refs.address.value)
  		if(bol1&&bol2&&bol3){
  			var add={'name':'','phone':'','address':'','def':false};
  			add.name=this.$refs.name.value
  			add.phone=this.$refs.phone.value
  			add.address=this.$refs.address.value
  			this.setAddress(add)
  			this.$router.go(-1)
  		}else{
  			 Toast({
			  message: '信息格式有误，请正确填写信息',
			  position: 'bottom',
			  duration: 2000,
			  className:'_toast'
			})
  		}
  	},
  	
  },
  components:{
  	'my-head':head
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.add
	padding 0 pxtorem(50px)
	padding-top pxtorem(100px)
	input
		display block
		width pxtorem(650px)
		height pxtorem(50px)
		border-bottom pxtorem(1px) solid #ccc
		margin pxtorem(30px) 0
		font-size pxtorem(30px)
		outline none
	textarea
		width pxtorem(650px)
		height pxtorem(250px)
		border none
		border-bottom pxtorem(1px) solid #ccc
		outline none
		font-size pxtorem(30px)
		font-family 'arail'
	div:nth-of-type(2)
		width pxtorem(250px)
		height pxtorem(70px)
		line-height pxtorem(70px)
		text-align center
		margin pxtorem(30px) auto
		font-size pxtorem(30px)
		background-color #292929
		color white
		border-radius pxtorem(10px)

</style>



