<template>
  <div class="search">
    <my-head :mes='title'></my-head>
	<div>
		<input type="text" placeholder='秋裤秋衣，你买得起吗？' ref='searchContent'>
		<input type="button" value="搜索" @click='searchFn'>
	</div>
	<ul ref='ul1'>
		<p>最近搜索</p>
		<li v-for='item in history' @click='pasteFn'>{{item}}</li>
	</ul>
	<list :lists='selected'></list>
  </div>
</template>

<script>
import head from '../head.vue'
import {mapActions,mapGetters} from 'vuex'
import goodlist from '../goodlist.vue'
export default {
  name: '',
  data () {
    return {
      	title:{'left':'返回','img':require('../../assets/img/back.png'),'center':''},
      	filterArr:[]
    }
  },
  computed:mapGetters({
  	history:'getSearchHistory',
  	goods:'getGoodList',
  	selected:'getSelected'
  }),
  methods:{
  	...mapActions([
  		'setSearchHistory',
  		'setSelected'
  	]),
  	pasteFn:function(){
  		this.$refs.searchContent.value=event.currentTarget.innerText
  	},
  	searchFn:function(){
  		//存历史
  		this.setSearchHistory(this.$refs.searchContent.value)
  		//搜索内容
  		var temp=[]
  		for(var i=0;i<this.goods.length;i++){
  			if(this.goods[i].name.indexOf(this.$refs.searchContent.value)>0){
  				temp.push(this.goods[i])
  			}
  		}
  		this.setSelected(temp)

  	}
  },
  components:{
  	'my-head':head,
  	'list':goodlist
  },
  mounted:function(){
  	this.setSelected('')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.search
	padding-top pxtorem(100px)
	>div:nth-of-type(2)
		width pxtorem(750px)
		height pxtorem(100px)
		background-color #f2f2f2
		input[type=text]
			width pxtorem(560px)
			height pxtorem(75px)
			margin pxtorem(12px) pxtorem(50px)
			margin-right pxtorem(20px)
			border-bottom pxtorem(1px) solid gray
			background-color transparent
			outline none
			font-size pxtorem(30px)
			background url('../../assets/img/searchIcon.png') no-repeat pxtorem(500px) pxtorem(10px)
			background-size 7.7%
			text-indent pxtorem(20px)
		input[type=button]
			width pxtorem(90px)
			height pxtorem(50px)
			border-radius pxtorem(10px)
			color #292929
			outline none
	>ul:nth-of-type(1)
		width pxtorem(750px)
		overflow hidden
		height pxtorem(291px)
		>p
			width pxtorem(750px)
			height pxtorem(60px)
			padding-top pxtorem(20px)
			background-color #f2f2f2
			border-top 1px solid #292929
			font-size pxtorem(30px)
			text-indent pxtorem(20px)
			margin-bottom pxtorem(50px)
			color gray
		>li
			height pxtorem(40px)
			font-family 'arail'
			font-size pxtorem(25px)
			float left
			padding pxtorem(5px) pxtorem(20px)
			background-color #ccc
			margin pxtorem(15px) pxtorem(15px)
			color #f2f2f2
			line-height pxtorem(40px)
			text-align center
			border-radius pxtorem(10px)
			
</style>



