<template>
  <div class="goodlist" >
    <div v-for='(item,index) in lists' @click='detailFn(index)'>
    	<img :src="item.des" alt="">
    	<div>
    		<p>{{item.name}}</p>
    		<span>￥{{item.price}}</span>
			<span>原价 ￥{{parseInt(44)}}</span>
    		<span>销量 0</span>
    	</div>
    	<slot name='load'></slot>
    </div>    
  </div>
</template>
<script>
import {mapGetters,mapActions} from 'vuex'
export default {
  props:['lists'],
  name: '',
  data () {
    return {
    }
  },
  computed:mapGetters({   
    goods:'getGoodList',
    selected:'getSelected'
    }),
  methods:{
  	...mapActions([
  		'setDetailData'
  	]),
  	detailFn:function(index){
        this.setDetailData(this.selected[index])
        this.$router.push('/detail')
    }   
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../assets/common.styl'
.goodlist
	width pxtorem(750px)
	>div
		width pxtorem(750px)
		height pxtorem(190px)
		overflow hidden
		border-bottom 1px solid #ccc
		img
			width pxtorem(150px)
			height pxtorem(150px)
			margin pxtorem(20px)
			display block
			float left
		div
			float left
			width pxtorem(520px)
			height pxtorem(150px)
			margin pxtorem(20px)
			p:nth-of-type(1)
				font-size pxtorem(30px)
				height pxtorem(60px)
				color #292929
				margin-bottom pxtorem(10px)
			span:nth-of-type(1)
				display block
				margin-bottom pxtorem(20px)
				width pxtorem(120px)
				font-size pxtorem(33px)
				color #FF6600
			span:nth-of-type(2)
				display inline-block
				margin-left pxtorem(10px)
				width pxtorem(400px)
				font-size pxtorem(25px)
				color #ccc		
			span:last-of-type				
				font-size pxtorem(30px)
				color gray
			
			
</style>



