import axios from 'axios'
export default{
	setSelectedCity({commit,state},data){
		commit('SELECTED_CITY',data)
	},
	setGoodList({commit,state}){
		axios.get('/goodList').then(function(res){
  			commit('GOODLIST',res)
  		}) 		
	},
  setChosenSort({commit,state},data){
    commit('CHOSENSORT',data)
  },
  setDetailData({commit,state},data){
    commit('DETAILGOOD',data)
  },
  setCartList({commit,state},data){
    commit('CARTLIST',data)
  },
  setSelected({commit,state},data){
    commit('SELECTED',data)
  },
  setSearchHistory({commit,satte},data){
    commit('SEARCHHISTORY',data)
  },
  setSelectedCoupon({commit,state},data){
    commit('SELECTEDCOUPON',data)
  },
  setStorage({commit,state},data){
    commit('STORAGE',data)
  },
  setAddress({commit,state},data){
    commit('ADDRESS',data)
  },
  setUnpaid({commit,state},data){
    commit('UNPAID',data)
  },
  setSelectedAdd({commit,state},data){
    commit('SELECTEDADD',data)
  },
  setCartEmpty({commit,state}){
    commit('CARTEMPTY')
  },
  setStates({commit,state},data){
    commit('STATES',data)
  },
  setCooldown1({commit,state},){
    commit('COOLDOWN1')
  },
  setCooldown2({commit,state},){
    commit('COOLDOWN2')
  },

}


