const SELECTED_CITY='SELECTED_CITY';
const GOODLIST='GOODLIST';
const CHOSENSORT='CHOSENSORT';
const DETAILGOOD='DETAILGOOD';
const CARTLIST='CARTLIST';
const SELECTED='SELECTED';
const SEARCHHISTORY='SEARCHHISTORY';
const SELECTEDCOUPON='SELECTEDCOUPON';
const STORAGE='STORAGE';
const ADDRESS='ADDRESS';
const UNPAID='UNPAID';
const SELECTEDADD='SELECTEDADD';
const CARTEMPTY='CARTEMPTY';
const STATES='STATES';
const COOLDOWN1='COOLDOWN1';
const COOLDOWN2='COOLDOWN2';
export default{
	[SELECTED_CITY](state,data){
		state.selectedCity=data.province.value+'-'+data.city.value
	},
	[GOODLIST](state,datas){
		state.goodList=datas.data.list
	},
	[CHOSENSORT](state,data){
		state.chosenSort=data
	},
	[DETAILGOOD](state,data){
		state.detailData=data
	},
	[CARTLIST](state,data){
		var bol=true;
		for(var i=0;i<state.cartList.length;i++){
			if(data[0].name==state.cartList[i][0].name){
				state.cartList[i][1]=parseInt(state.cartList[i][1])+parseInt(data[1])
				bol=false
			}
		}
		if(bol){state.cartList.push(data)}
	},
	[SELECTED](state,data){
		state.selected=data
	},
	[SEARCHHISTORY](state,data){
		if(state.searchHistory.indexOf(data)<0){
			state.searchHistory.unshift(data)
		}
		if(state.searchHistory.length>15){
			state.searchHistory.pop()
		}
		
	},
	[SELECTEDCOUPON](state,data){
		state.selectedCoupon=data
	},
	[STORAGE](state,data){
		var bol=true
		for(var i=0;i<state.storage.length;i++){
			if(state.storage[i].name==data.name){
				bol=false
			}
		}
		console.log(bol)
		if(bol){
			state.storage.push(data)
		}
	},
	[ADDRESS](state,data){
		state.address.push(data)
	},
	[UNPAID](state,data){
		state.unpaid.push(data)
	},
	[SELECTEDADD](state,data){
		state.selectedAdd=data
	},
	[CARTEMPTY](state){
		state.cartList=[]
	},
	[STATES](state,data){
		state.states=data
	},
	[COOLDOWN1](state){
		if(state.cooldown1=='获取验证码'){
			state.cooldown1='60s后再发送'
			var t=60;
			var timer1=setInterval(function(){
				t--
				if(t<10){
					state.cooldown1='0'+t+'s后再发送'
				}else{
					state.cooldown1=t+'s后再发送'
				}
				if(t==0){
					clearInterval(timer1)
					state.cooldown1='获取验证码'
				}
			},1000)
		}
	},
	[COOLDOWN2](state){
		if(state.cooldown2=='获取验证码'){
			state.cooldown2='60s后再发送'
			var t=60;
			var timer2=setInterval(function(){
				t--
				if(t<10){
					state.cooldown2='0'+t+'s后再发送'
				}else{
					state.cooldown2=t+'s后再发送'
				}
				if(t==0){
					clearInterval(timer2)
					state.cooldown2='获取验证码'
				}
			},1000)
		}
	},
}


