<template>
  <div class="forget">
    <my-head :mes='title'></my-head>
    <form action="">
      <input type="text" placeholder='电话号码'>
      <input type="text" placeholder='验证码'>
      <input type="button" :value="send" @click='getFn' ref='getting'>
      <input type="text" placeholder='重置密码'>
      <input type="text" placeholder='再次输入新密码'>
      <input type="button" value="确 认">
    </form>
  </div>
</template>

<script>
import head from '../head.vue'
import {mapActions,mapGetters} from 'vuex'
export default {
  name: '',
  data () {
    return {
      	title:{'left':'返回','img':require('../../assets/img/back.png'),'center':'忘记密码'}
    }
  },
  components:{
  	'my-head':head
  },
  computed:mapGetters({
    send:'getCooldown1'
  }),
  methods:{
    ...mapActions([
      'setCooldown1'
    ]),
    getFn:function(){
      this.setCooldown1()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.forget
  padding-top pxtorem(100px)
  width pxtorem(750px)
  height pxtorem(1234px)
  background-color #f2f2f2
  text-align center
  input
    width pxtorem(650px)
    height pxtorem(100px)
    margin-top pxtorem(30px)
    border pxtorem(1px) solid #ccc
    font-size pxtorem(30px)
    text-indent pxtorem(30px)
    border-radius pxtorem(10px)
    outline none
  input[type=button]
    text-indent 0
    color white
    background-color #292929
  input:nth-of-type(2)
    width pxtorem(350px)
    margin-right pxtorem(46px)
  input:nth-of-type(3)
    width pxtorem(250px)
  
</style>



