<template>
  <div class="storage">
    <my-head :mes='title'></my-head>
    <list :lists='selected'></list>
  </div>
</template>

<script>
import head from '../head.vue'
import goodlist from '../goodlist'
import {mapActions,mapGetters} from 'vuex'
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../../assets/img/back.png'),'center':'我的收藏'}
    }
  },
  computed:mapGetters({
  	storage:'getStorage',
  	selected:'getSelected'
  }),
  methods:{
  	...mapActions([
  		'setSelected'
  	]),
  },
  components:{
  	'my-head':head,
  	'list':goodlist
  },
  mounted:function(){
  		this.setSelected(this.storage)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.storage
	padding-top pxtorem(100px)
</style>



