<template>
  <div class="nav">
    <div v-for='(item,index) in navArr' @click='goFn(index)'>
    	<img :src="item.url" alt="">
    	<p ref='nav_p'>{{item.name}}</p>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import { Toast } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
      	navArr:[
      		{'url':require("../assets/img/footer01.png"),'name':'首页','link':'/'},
      		{'url':require("../assets/img/footer02.png"),'name':'分类','link':'/sort'},
      		{'url':require("../assets/img/footer03.png"),'name':'购物车','link':'/car'},
      		{'url':require("../assets/img/footer04.png"),'name':'我的','link':'/mine'}
      	],
      	pageArr:['/','/sort','/car','/mine']
    }
  },
  computed:mapGetters({
    states:'getStates'
  }),
  methods:{
  	goFn:function(index){
      var that=this
      if(index==2||index==3){
        if(this.states==''){
            Toast({
              message: '请先登录',
              iconClass: 'icon icon-success',
              duration:500
            });
            setTimeout(function(){
              that.$router.push('/log')
            },500)
        }else{
            this.$router.push(this.navArr[index].link)
        }
      }else{
          this.$router.push(this.navArr[index].link)
      }
      
    }
  },
  mounted:function(){
  	var tag=this.pageArr.indexOf(this.$route.fullPath);
  	this.navArr[tag].url=require("../assets/img/footer00"+(tag+1)+".png")
  	this.$refs.nav_p[tag].style.color='red';
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../assets/common.styl'
.nav
  position fixed
  bottom 0
  left 0
  width pxtorem(750px)
  height pxtorem(120px)
  background-color #f2f2f2
  display flex
  z-index 99
  div
    flex 1
    img
      display block
      margin pxtorem(10px) auto
      height pxtorem(60px)
    p
      text-align center
      color gray
  
</style>



