<template>
  <div class="home">
    <my-nav></my-nav>
    <my-head :mes='title'></my-head>
    <img src="../../assets/img/search.png" alt="" class="search" @click='searchFn'>
    <mt-swipe :auto="4000" class='cycle'>
        <mt-swipe-item v-for='item in cyArr'><img v-bind:src="item.src" alt=""></mt-swipe-item>
    </mt-swipe>
    <div class="list" >
        <div v-for='(item,index) in listArr' @click='toFn(index)'>
            <img :src="item.img" alt="">
            <p>{{item.p}}</p>
        </div>
    </div>
    <router-link class="address" tag='div' :to="'/citylist'">
        <img src="../../assets/img/address.png" alt="">
        <span>所在城市：{{selectedCity}}</span>
        <div>选择城市>></div>
    </router-link>
    <div class='blank'></div>
    <div class="coupon">
        <img :src="item.url" alt="" v-for='(item,index) in coupons' @click='couponFn(index)'>
    </div>
    <div class="blank"></div>
    <img :src="leader.onSale" alt="" class='leader'>
    <goodlist :lists='[goods[0]]'>      
    </goodlist>
    <div class="s-blank"></div>
    <img :src="leader.hotSale" alt="" class='leader'>
    <div class="hotSale">
        <div>
            <div v-for='(item,index) in some' @click='someFn(index)'>
                <img :src="item.des" alt="">
                <p>{{item.name}}</p>
                <span>￥{{item.price.toFixed(2)}}</span>
                <span>销量 0</span>
            </div>
        </div>
    </div>
    <div class="s-blank"></div>
    <img :src="leader.discout" alt="" class='leader'>
    <goodlist :lists='list' >        
    </goodlist>
    <infinite-loading @infinite="infiniteHandler" >
        <span slot="no-more" ref='no_more'>亲~已经到底啦...</span>
    </infinite-loading>
  </div>
</template>

<script>
import nav from '../nav.vue'
import head from '../head.vue'
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
//js的引入
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
//css的引入
import 'mint-ui/lib/style.css'
import {mapGetters,mapActions} from 'vuex'
import goodlist from '../goodlist.vue'
import InfiniteLoading from 'vue-infinite-loading'
import {bus} from '../../bus/bus.js'
import axios from 'axios'
export default {
  name: '',
  data () {
    return {
      title:{'center':'生活购','left':'','img':'###'},
      cyArr:[
        {'src':require('../../assets/img/cycle1.jpg')},
        {'src':require('../../assets/img/cycle2.jpg')},
        {'src':require('../../assets/img/cycle3.jpg')}
      ],
      listArr:[
        {'img':require('../../assets/img/list1.png'),'p':'水果'},
        {'img':require('../../assets/img/list2.png'),'p':'休闲食品'},
        {'img':require('../../assets/img/list3.png'),'p':'米谷粮油'},
        {'img':require('../../assets/img/list4.png'),'p':'酒水饮料'},
        {'img':require('../../assets/img/list5.png'),'p':'清洁纸品'},
        {'img':require('../../assets/img/list6.png'),'p':'全部'}
      ],
      list:[]
    }
  },
  components:{
  	'my-nav':nav,
  	'my-head':head,
    'goodlist':goodlist,
    InfiniteLoading
  },
  computed:mapGetters({
    selectedCity:'getSelectedCity',
    coupons:'getCoupons',
    leader:'getLeader',
    goods:'getGoodList',
    some:'getSomeArr',
    }),
  methods:{
    ...mapActions([
        'setGoodList',
        'setChosenSort',
        'setSelectedCoupon',
        'setSelected',
        'setDetailData'
    ]),
    infiniteHandler($state) {
        if(this.list.length==this.goods.length){
            $state.complete();
            this.$refs.no_more.style.fontSize='25px';
        }else{
            setTimeout(() => {
                var temp = [];
                for (let i = this.list.length; i<=this.list.length + 5&&i<this.goods.length; i++) {
                  temp.push(this.goods[i]);
                }
                this.list = this.list.concat(temp);
                $state.loaded();
              }, 500);
        }        
    },
    searchFn:function(){
        this.$router.push('/search')
    },
    toFn:function(index){
        this.setChosenSort(index)
        this.$router.push('/sort')
    },
    couponFn:function(index){
        this.setSelectedCoupon(this.coupons[index])
        this.$router.push('/coupon')

    },
    someFn:function(index){
        this.setDetailData(this.some[index])
        this.$router.push('/detail')
    }
  },
  mounted:function(){
    this.setGoodList()
    var that=this
    axios.get('/goodList').then(function(res){
            that.setSelected(res.data.list)
        })  
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.home
    padding-bottom pxtorem(160px)
    padding-top pxtorem(100px)
    .cycle
        width pxtorem(750px)
        height pxtorem(375px)
        div
            img
                width pxtorem(750px)
    .list
        overflow hidden
        border-bottom pxtorem(1px) solid #ccc
        width pxtorem(750px)
        div
            float left
            width pxtorem(250px)
            text-align center
            img
                height pxtorem(100px)
                width pxtorem(100px)
                display block
                margin auto
            p
                margin pxtorem(-10px) 0 pxtorem(20px) 0
                color rgb(102, 102, 102)
.search
    width pxtorem(577px)
    display block
    position absolute
    left pxtorem(86.5px)
    top pxtorem(130px)
    z-index 99
.address
    width pxtorem(750px)
    height pxtorem(80px)
    background-color #fdfeff
    overflow hidden
    img
        display block
        float left
        width pxtorem(46px)
        margin pxtorem(19px) pxtorem(30px)
    span
        display block
        float left
        width pxtorem(480px)
        height pxtorem(80px)
        line-height pxtorem(80px)
        font-size pxtorem(30px)
        color rgb(102,102,102)
    div
        width pxtorem(150px)
        height pxtorem(80px)
        line-height pxtorem(80px)
        float left
        font-size pxtorem(25px)
        color #ccc
.blank
    height pxtorem(30px)
    background-color #f2f2f2
.s-blank
    height pxtorem(10px)
    background-color #f2f2f2
.coupon
    width pxtorem(750px)
    overflow hidden
    img
        display block
        float left
        width pxtorem(320px)
        height pxtorem(141px)
        margin pxtorem(10px) pxtorem(27px)
.leader
    width pxtorem(750px)
    height pxtorem(291px)
.hotSale
    width pxtorem(750px)
    height pxtorem(460px)
    overflow-x scroll
    overflow auto
    div
        width pxtorem(1500px)
        div
            width pxtorem(300px)
            height pxtorem(400px)
            margin pxtorem(25px) pxtorem(37px)
            float left
            img
                width pxtorem(300px)
                height pxtorem(300px)
                display block
            p
                font-size pxtorem(28px)
                overflow hidden
                text-overflow ellipsis
                white-space nowrap
                width pxtorem(300px)
                font-family 'arail'
                margin pxtorem(20px) 0
                color #292929
            span:nth-of-type(1)
                color #FF6600
                font-size pxtorem(30px)
                display inline-block
                width pxtorem(166px)
            span:nth-of-type(2)
                color gray               
      
</style>



