<template>
  <div class="address">
    <my-head :mes='title'></my-head>
    <ul>
    	<li v-for='(item,index) in getAdd'>
    		<p>
    			<span>{{item.name}}</span>
    			<span>{{item.phone}}</span>
    		</p>
    		<p>{{item.address}}</p>
    		<div @click='defaultFn(index)'>
    			<div ref='def'><img src="../../assets/img/checked.png" alt="" ></div>
    			<span>设为默认</span>
    		</div>
    		<img src="../../assets/img/delete.png" alt="" @click='deleteFn(index)'>
    	</li>
    </ul>
    <p @click='addAddressFn'>
    	<span> + </span>添加新地址
    </p>
  </div>
</template>

<script>
import head from '../head.vue'
import {mapActions,mapGetters} from 'vuex'
import { MessageBox } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../../assets/img/back.png'),'center':'我的地址'}
    }
  },
  computed:mapGetters({
  	getAdd:'getAddress'
  }),
  methods:{
  	...mapActions([
  		'setAddress',
  		'setSelectedAdd'
  	]),
  	defaultFn:function(index){
  		for(var i=0;i<this.getAdd.length;i++){
  			if(i==index){
  				this.getAdd[i].def=true
  			}else{
  				this.getAdd[i].def=false
  			}
  		}
  		this.changedefFn()
  		this.setSelectedAdd('')
  	},
  	changedefFn:function(){
  		for(var i=0;i<this.getAdd.length;i++){
	  		if(this.getAdd[i].def){
	  			this.$refs.def[i].style.backgroundColor='orange'
	  		}else{
				this.$refs.def[i].style.backgroundColor='#ccc'
	  		}
	  	}
  	},
  	addAddressFn:function(){
  		this.$router.push('/addAddress');
  	},
  	deleteFn:function(index){
  		MessageBox.confirm('删除无法恢复').then(action => {
		  this.getAdd.splice(index,1)
		});
  	}
  },
  components:{
  	'my-head':head
  },
  mounted:function(){
  	this.changedefFn()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.address
	padding-top pxtorem(100px)
	ul
		width pxtorem(750px)
		li
			width pxtorem(650px)
			height pxtorem(170px)
			padding pxtorem(25px) pxtorem(50px)
			border-bottom pxtorem(1px) solid #ccc
			color gray
			>p:nth-of-type(1)
				height pxtorem(50px)
				overflow hidden
				>span:nth-of-type(1)
					font-size pxtorem(30px)
					float left
				>span:nth-of-type(2)
					font-size pxtorem(30px)
					float right
			>p:nth-of-type(2)
				height pxtorem(60px)
				font-size pxtorem(28px)
				margin-top pxtorem(10px)
			>div
				display inline-block
				width pxtorem(200px)
				height pxtorem(50px)
				overflow hidden
				line-height pxtorem(50px)
				font-size pxtorem(30px)
				>div
					float left
					width pxtorem(40px)
					height pxtorem(40px)
					background-color #ccc
					border-radius 50%
					text-align center
					line-height pxtorem(40px)
					margin pxtorem(5px) pxtorem(0px)
					margin-right pxtorem(10px)
					img
						width pxtorem(30px)
				>span
					float left
			>img
				margin-left pxtorem(400px)
				width pxtorem(40px)
				vertical-align center
	>p
		position fixed
		bottom 0
		left 0
		width pxtorem(750px)
		height pxtorem(100px)
		border-top pxtorem(1px) solid #ccc
		line-height pxtorem(100px)
		text-align center
		font-size pxtorem(32px)
		color #666666
		>span
			font-size pxtorem(40px)
			font-weight bold
			color gray 
</style>



