<template>
  <div class="mine">
    <my-nav></my-nav>
    <my-head :mes='title'></my-head>
    <div class="me_head">
      <div></div>
      <p>浪小姐</p>
      <div>VIP会员</div>   
    </div>
    <div class="me_list">
    <ul>
        <li v-for='(item,index) in listArr' @click='openFn(index)'>
          <img :src="item.url1" alt="">
          <span>{{item.text}}</span>
        </li>
    </ul>
    <div class="order">
      <div>
        <div ref='order' v-for='(item,index) in orderSet' @click='switchFn(index)'>{{item}}</div>        
      </div>
    </div>   
  </div>
  <div ref='unpaid1' class="unpaid">
    <ul v-for='(x,index) in unpaid' >
      <p>订单号：00000000{{index+1}} <img src="../../assets/img/delete.png" alt="" @click='delUnpaidFn(index)'></p>
      <li v-for='(item,index2) in unpaid[index][1]'>
        <img :src="item[0].des" alt="">
        <div>
          <p>{{item[0].name}}</p>
          <p>数量{{item[1]}}
            <span>￥{{item[0].price}}/件</span>
          </p>
        </div>
      </li>
      <p>共{{x[1].length}}件商品，运费￥10元
        <span>实付：￥{{x[0]}}</span>
      </p>
      <p>订单状态：待付款
          <button @click='repayFn(index)'>付 款</button>
      </p>
    </ul>
  </div>
  <div ref='paid1'>
    <ul>
      <li></li>
    </ul>
  </div>
  </div>
</template>

<script>
import nav from '../nav.vue'
import head from '../head.vue'
import {mapGetters,mapActions} from 'vuex'
import { MessageBox } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
      title:{'center':'我的'},
      orderSet:['全部订单','待付款','待收货'],
      listArr:[
        {'url1':require('../../assets/img/data.png'),'text':'基本资料'},
        {'url1':require('../../assets/img/storage.png'),'text':'我的收藏'},
        {'url1':require('../../assets/img/address1.png'),'text':'我的地址'}
        ],
      orderSelected:0
    }
  },
  computed:mapGetters({
    unpaid:'getUnpaid',
    states:'getStates'
  }),
  components:{
  	'my-nav':nav,
    'my-head':head
  },
  methods:{
    ...mapActions([
      'setUnpaid'
    ]),
    switchFn:function(index){
      for(var i=0;i<this.orderSet.length;i++){
        if(i==index){
          this.$refs.order[i].style.backgroundColor='#292929'
          this.$refs.order[i].style.color='white'
          if(i==0){
            this.$refs.unpaid1.style.display=this.$refs.paid1.style.display='block'
          }else if(i==1){
            this.$refs.unpaid1.style.display='block'
            this.$refs.paid1.style.display='none'
          }else if(i==2){
            this.$refs.unpaid1.style.display='none'
            this.$refs.paid1.style.display='block'
          }
        }else{
          this.$refs.order[i].style.backgroundColor='white'
          this.$refs.order[i].style.color='#292929'
        }
      }
    },
    EmptyOrderFn:function(){

    },
    delUnpaidFn:function(index){
      MessageBox.confirm('确认删除此订单？').then(action => {
        this.unpaid.splice(index,1)
      });
    },
    openFn:function(index){
      if(index==0){
        this.$router.push('/information')
      }else if(index==1){
        this.$router.push('/storage')
      }else if(index==2){
        this.$router.push('/address')
      }
    },
    repayFn:function(index){
      var m=this.unpaid[index]
      this.unpaid.splice(index,1)
      this.unpaid.push(m)
      this.$router.push('/order')
    }
  },
  mounted:function(){
      if(this.states==''){
        this.$router.push('/log')
      }
      this.$refs.order[this.orderSelected].style.backgroundColor='#292929'
      this.$refs.order[this.orderSelected].style.color='white'
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.mine
  padding pxtorem(100px) 0 pxtorem(120px) 0
  background-color #f2f2f2
  width pxtorem(750px)
  min-height pxtorem(1334px)
  .me_head
    width pxtorem(750px)
    height pxtorem(350px)
    background url(../../assets/img/bg.png) no-repeat
    background-size 100%
    background-color #e7e7e7
    overflow hidden
    div:nth-child(1)
      width pxtorem(155px)
      height pxtorem(155px)
      border-radius 50%
      border 3px solid white
      background url(../../assets/img/beauty.png) pxtorem(-70px)  pxtorem(-50px)
      background-size pxtorem(300px)    
      margin auto
      margin-top pxtorem(30px)
    p
      text-align center
      color white
      font-size pxtorem(35px)
      font-family "华文细黑"
      margin pxtorem(20px) 0
    div:nth-child(3)
      width pxtorem(140px)
      height pxtorem(40px)
      border-radius pxtorem(20px)
      border 2px solid white
      margin auto
      line-height pxtorem(40px)
      color white
      background rgba(255,255,255,0.2)
      text-align center
  .me_list
    ul
      overflow hidden
      width pxtorem(750px)
      border-bottom 1px solid #292929
      li
        height pxtorem(90px)
        width pxtorem(190px)
        overflow hidden
        text-align left
        padding 0 pxtorem(30px)
        line-height pxtorem(90px)
        float left
        box-sizing padding-box
        background-color #f2f2f2
        img:nth-child(1)
          width pxtorem(35px)
          margin-top pxtorem(25px)
          margin-right pxtorem(5px)
          float left
        span
        color #1a1a1a
        font-size pxtorem(28px)
      li:nth-of-type(3)
        >img
          width pxtorem(28px)         
  .order
    width pxtorem(750px)
    height pxtorem(90px)
    border-bottom pxtorem(1px) solid #292929
    background-color white
    >div
      width pxtorem(718px)
      height pxtorem(70px)
      border pxtorem(1px) solid #292929
      margin pxtorem(15px)
      border-radius pxtorem(30px)
      overflow hidden
      >div
        color #292929
        width pxtorem(239.3px)
        height pxtorem(70px)
        box-sizing border-box
        float left
        line-height pxtorem(70px)
        text-align center
        font-size pxtorem(30px)
      >div:nth-of-type(2)
        border-left pxtorem(1px) solid #292929
        border-right pxtorem(1px) solid #292929
  .unpaid
    >ul
      margin-top pxtorem(30px)
      background-color white
      >p:nth-of-type(1)
        width pxtorem(730px)
        height pxtorem(50px)
        border-top pxtorem(1px) solid gray
        border-bottom pxtorem(1px) solid gray
        line-height pxtorem(50px)
        padding-left pxtorem(20px)
        color gray
        img
          width pxtorem(40px)
          vertical-align middle
          margin-left pxtorem(430px)
      li
        width pxtorem(750px)
        height pxtorem(190px)
        border-bottom pxtorem(1px) solid #ccc
        overflow hidden
        >img
          width pxtorem(150px)
          height pxtorem(150px)
          margin pxtorem(20px)
          display block
          float left
        >div
          width pxtorem(560px)
          height pxtorem(150px)
          padding pxtorem(20px) 0
          float left
          >p:nth-of-type(1)
            margin-top pxtorem(0)
            font-size pxtorem(30px)
            width pxtorem(560px)
            overflow hidden
            text-overflow ellipsis
            white-space nowrap
            color #292929          
          >p:nth-of-type(2)
            font-size pxtorem(25px)
            height pxtorem(35px)
            color #999
            margin-top pxtorem(70px)
            overflow hidden
            padding-right pxtorem(20px)
            span
              float right
              font-size pxtorem(33px)
              color #CE3B41
      >p:nth-of-type(2)
        font-size pxtorem(25px)
        line-height pxtorem(50px)
        padding-left pxtorem(20px)
        height pxtorem(50px)
        color gray
        border-bottom pxtorem(1px) solid #ccc
        overflow hidden
        padding-right pxtorem(20px)
        span
          float right
          font-size pxtorem(30px)
          color #CE3B41
      >p:nth-of-type(3)
        font-size pxtorem(25px)
        line-height pxtorem(50px)
        padding-left pxtorem(20px)
        height pxtorem(50px)
        color #CE3B41
        border-bottom pxtorem(1px) solid #ccc
        >button
          width pxtorem(120px)
          height pxtorem(40px)
          display inline-block
          vertical-align top
          margin pxtorem(5px) 0 0 pxtorem(380px)
          border none
          background-color #ce3b41
          color white
          border-radius pxtorem(5px)
          font-size pxtorem(23px)

        
        
</style>



