<template>
  <div class="detail">
  	<my-head :mes='title'></my-head>
	<mt-swipe :auto="2000" class='cycle'>
        <mt-swipe-item v-for='item in [1,2,3]'><img v-bind:src="good.des" alt=""></mt-swipe-item>
    </mt-swipe>
    <p>{{good.name}}</p>
    <p>￥{{good.price}}</p>
    <p>
	    <span>库存：9999</span>
	    <span>运费：10</span>
	    <span>销量：0</span>
    </p>
    <div class="bottom">
    	<div @click="toCartFn"><img src="../assets/img/shop.png" alt=""><p>购物车</p></div>
    	<div @click='storageFn'><img src="../assets/img/fl.png" alt="" ref='storei'><p ref='storep'>收藏</p></div>
    	<p @click='addFn'>加入购物车</p>
    	<p @click='buyFn'>立即购买</p>
    </div>
    <div class="insurance">
    	<p>售后保障</p>
    	<img src="../assets/img/f1.png" alt="">
    	<img src="../assets/img/f2.png" alt="">
    	<img src="../assets/img/f3.png" alt="">
    </div>
    <div class="about">
    	<div :class="bol.left" @click='moreFn'>商品详情</div>
    	<div :class="bol.right" @click='comFn'>用户评价</div>
    </div>
    <div class="more_detail"  :class='show.left'><img :src="good.detail" alt=""></div>
    <div class="comments"  :class='show.right'>
    	<ul>
    		<li v-for='item in [1,1,1,1,1,1,1]'>
    			<div></div>
    			<div>
    				<p>匿名用户★★★★★<span>2011-11-11</span></p>
    				<p>若要人不知，除非己莫为</p>
    			</div>
    		</li>
    	</ul>
    </div>
    <div class="check" v-show='checkBol.show'>
    	<div><span @click='closeFn'>X</span></div>
    	<img :src="good.des" alt="">
    	<div>
    		<p>{{good.name}}</p>
    		<span>￥ {{good.price}}</span>
    		<button @click='minusFn'>-</button>
    		<input type="text" value=1 ref='num'>
    		<button @click='plusFn'>+</button>
    		<div @click='finishFn'>确认</div>
    	</div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
//js的引入
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
//css的引入
import 'mint-ui/lib/style.css'
import {mapGetters} from 'vuex'
import head from './head.vue'
import {mapActions} from 'vuex'
import { Toast } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
      title:{'center':'商品详情','left':'返回','img':require('../assets/img/back.png')},
      bol:{'left':'under','right':''},
      show:{'left':'','right':'none'},
      checkBol:{'show':false,'way':''},
      toasts:[]
    }
  },
  computed:mapGetters({
  	good:'getDetailData',
  	unpaid:'getUnpaid',
  	states:'getStates'
  }),
  components:{
  	'my-head':head
  },
  methods:{
  	...mapActions([
  		'setCartList',
  		'setStorage',
  		'setUnpaid'
  	]),
  	moreFn:function(){
  		this.bol={'left':'under','right':''}
  		this.show={'left':'','right':'none'}
  	},
  	comFn:function(){
  		this.bol={'left':'','right':'under'}
  		this.show={'left':'none','right':''}
  	},
  	addFn:function(){
  		var that=this
  		this.logStates(function(){
  			that.checkBol={'show':true,'way':'add'}
  		}) 		
  	},
  	buyFn:function(){
  		var that=this
  		this.logStates(function(){
  			that.checkBol={'show':true,'way':'buy'}
  		}) 
  	},
  	closeFn:function(){
  		this.checkBol={'show':false,'way':''}
  	},
  	plusFn:function () {
  		this.$refs.num.value++
  	},
  	minusFn:function () {
  		if(this.$refs.num.value<=1){
  			this.$refs.num.value=1
  		}else{
  			this.$refs.num.value--
  		}
  	},
  	logStates:function(cb){
  		var that=this
  		if(this.states==''){
  			Toast({
              message: '请先登录',
              duration:500
            });
            setTimeout(function(){
              that.$router.push('/log')
            },500)
  		}else{
  			cb()
  		}
  	},
  	finishFn:function () {
  		var pack=[];
		pack.push(this.good)
		pack.push(this.$refs.num.value)
  		if(this.checkBol.way=='add'){ 			
  			this.setCartList(pack)
  			Toast({
			  message: '添加成功',
			  iconClass: 'icon icon-success',
			  duration:1000
			});  			 			
  		}else if(this.checkBol.way=='buy'){
  			var sum=this.$refs.num.value*this.good.price
  			this.setUnpaid([sum,[pack]]);
  			this.$router.push('/order');
  		}
  		this.checkBol={'show':false,'way':''} 
  	},
  	toCartFn:function () {
  		var that=this
  		this.logStates(function(){
  			that.$router.push('/car')
  		}) 
  	},
  	storageFn:function(){
  		var that=this
  		this.logStates(function(){
  			if(that.$refs.storep.style.color!='orange'){
	  			that.$refs.storep.style.color='orange'
	  			that.$refs.storei.src=require('../assets/img/fl_a.png')
	  			that.setStorage(that.good)
	  			Toast({
				  message: '收藏成功',
				  iconClass: 'icon icon-success',
				  duration:1000
				});
	  		}else{
	  			Toast({
				  message: '请勿重复添加',
				  iconClass: 'icon icon-success',
				  duration:1000
				});
	  		}
  		}) 
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../assets/common.styl'
.detail
	padding pxtorem(100px) 0
	.cycle
		width pxtorem(750px)
		margin auto
		height pxtorem(550px)
		div
			img
				width pxtorem(750px)
				height pxtorem(550px)
				display block
	>p:nth-of-type(1)
		width pxtorem(710px)
		height pxtorem(80px)
		font-size pxtorem(33px)
		padding pxtorem(20px)
		padding-bottom 0
		color #292929
	>p:nth-of-type(2)
		width pxtorem(710px)
		height pxtorem(100px)
		font-size pxtorem(45px)
		padding 0 pxtorem(20px)
		color orange
	>p:nth-of-type(3)
		width pxtorem(750px)
		overflow hidden
		padding-bottom pxtorem(20px)
		border-bottom 1px solid #ccc
		span
			float left
			width pxtorem(250px)
			text-indent pxtorem(30px)
			font-size pxtorem(25px)
			color gray
	.bottom
		width pxtorem(750px)
		height pxtorem(100px)
		position fixed
		bottom 0
		left 0
		border-top 1px solid #ccc
		overflow hidden
		background-color white
		>div
			width pxtorem(120px)
			height pxtorem(100px)
			border-right 1px solid #ccc
			box-sizing border-box
			float left
			img
				height pxtorem(55px)
				margin auto
				margin-top pxtorem(5px)
				display block
			p
				font-size pxtorem(25px)
				text-align center
				width pxtorem(120px)
				margin-top pxtorem(10px)
				color gray
		>p
			width pxtorem(254px)
			height pxtorem(100px)
			color white
			font-size pxtorem(35px)
			background-color orange
			text-align center
			line-height pxtorem(100px)
			float left
			font-family '宋体'
		>p:nth-of-type(2)
			background-color #FF7100
	.insurance
		width pxtorem(750px)
		overflow hidden
		p
			font-size pxtorem(25px)
			background-color #f2f2f2
			padding pxtorem(10px) pxtorem(20px)
			color gray
		img
			width pxtorem(250px)
			display block
			float left 
	.about
		width pxtorem(700px)
		padding 0 pxtorem(25px)
		height pxtorem(100px)
		background-color #f2f2f2
		overflow hidden
		border-top 1px solid #ccc
		margin-top pxtorem(30px)
		>div
			float left
			width pxtorem(200px)
			box-sizing border-box
			font-size pxtorem(35px)
			text-align center
			height pxtorem(50px)
			margin pxtorem(25px) pxtorem(75px) 
			color #292929
		.under
			border-bottom pxtorem(5px) solid #F5A623
	.more_detail
		width pxtorem(750px)
		img
			width pxtorem(750px)
	.comments
		ul
			padding-top pxtorem(30px)
			li
				width pxtorem(750px)
				height pxtorem(150px)
				overflow hidden
				border-bottom 1px solid #ccc
				>div:nth-of-type(1)
					width pxtorem(100px)
					height pxtorem(100px)
					background url('../assets/img/customer.png') no-repeat pxtorem(10px) pxtorem(10px)
					background-size pxtorem(80px)
					background-color #f2f2f2
					margin pxtorem(25px) pxtorem(30px)
					float left
				>div:nth-of-type(2)
					width pxtorem(590px)
					height pxtorem(150px)
					float left
					>p:nth-of-type(1)
						font-size pxtorem(30px)
						padding pxtorem(25px) 0	
						color gray
						overflow hidden
						span
							color #ccc
							font-size pxtorem(25px)
							float right	
							padding-right pxtorem(20px)
					>p:nth-of-type(2)
						color #292929
	.none
		display none
	.check
		width pxtorem(750px)
		height pxtorem(300px)
		border-radius pxtorem(20px) pxtorem(20px) 0 0
		overflow hidden
		position fixed
		bottom pxtorem(100px)
		left 0
		background-color white
		>div:nth-of-type(1)
			width pxtorem(710px)
			padding 0 pxtorem(20px)
			overflow hidden
			background-color #f2f2f2
			height pxtorem(40px)
			line-height pxtorem(40px)
			span
				float right
		>img
			float left
			width pxtorem(200px)
			height pxtorem(200px)
			margin pxtorem(30px)
		>div:nth-of-type(2)
			width pxtorem(470px)
			height pxtorem(240px)
			float left
			padding-top pxtorem(20px)
			overflow hidden
			>p
				font-size pxtorem(30px)
				margin-bottom pxtorem(30px)
				height pxtorem(60px)
			>span
				font-size pxtorem(45px)
				color #FF7100
				float left
				margin-right pxtorem(150px)
				width pxtorem(150px)
			>button
				width pxtorem(40px)
				height pxtorem(40px)
				font-size pxtorem(30px)
				float left
			>input[type=text]
				width pxtorem(50px)
				height pxtorem(40px)
				text-align center
				border 1px solid #ccc
				box-sizing border-box
				float left
			>div:nth-of-type(1)
				clear both
				display block
				width pxtorem(150px)
				height pxtorem(60px)
				background-color #ED213A
				color white
				margin-top pxtorem(100px)
				margin-left pxtorem(290px)
				font-size pxtorem(30px)
				text-align center
				line-height pxtorem(60px)
				border-radius pxtorem(10px)
			
					
</style>



