<template>
  <div class="sss">
    <my-head :mes='title'></my-head>
    <div class='li' v-for='(item,index) in getAdd' @click='thisAdd(index)'>
    	<p>
	    	<span>{{item.name}}</span>
	    	<span>{{item.phone}}</span>
    	</p>
    	<p>{{item.address}}</p>
    	<img src="../assets/img/right.png" alt="">
    </div>
    <button @click='toChargeFn'>管理我的收货地址</button>
  </div>
</template>

<script>
import {mapGetters,mapActions} from 'vuex'
import head from './head.vue'
export default {
  name: '',
  data () {
    return {
      title:{'left':'返回','img':require('../assets/img/back.png')}
    }
  },
  computed:mapGetters({
  	getAdd:'getAddress'
  }),
  methods:{
  	...mapActions([
  		'setSelectedAdd'
  	]),
  	thisAdd:function(index){
  		this.setSelectedAdd(this.getAdd[index])
  		this.$router.go(-1)
  	},
  	toChargeFn:function(){
  		this.$router.push('/address')
  	}
  },
  components:{
  	'my-head':head
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../assets/common.styl'
.sss
	padding-top pxtorem(100px)
	.li
		width pxtorem(710px)
		height pxtorem(102px)
		padding pxtorem(32px) pxtorem(20px)
		border-bottom pxtorem(1px) solid gray
		position relative 
		p:nth-of-type(1)
			overflow hidden
			span
				float left
				width pxtorem(200px)
				font-size pxtorem(30px)
				color gray
		p:nth-of-type(2)
			margin-top pxtorem(30px)
			font-size pxtorem(30px)
			color gray
		>img
			position absolute
			width pxtorem(20px)
			right pxtorem(20px)
			top pxtorem(60px)
	button
		position fixed
		width pxtorem(500px)
		height pxtorem(77px)
		bottom pxtorem(50px)
		left pxtorem(125px)
		font-size pxtorem(33px)
		background-color #ee4147
		border none
		border-radius pxtorem(15px)
		color white
</style>



