<template>
  <div class="information">
    <my-head :mes='title'></my-head>
	<ul>
		<li>头像<img src="../../assets/img/right.png" alt=""><div></div></li>
		<li>姓名<img src="../../assets/img/right.png" alt=""><span>浪小姐</span></li>
		<li>绑定号码<img src="../../assets/img/right.png" alt=""><span>18828886888</span></li>
	</ul>
  </div>
</template>

<script>
import head from '../head.vue'
export default {
  name: '',
  data () {
    return {
      	title:{'left':'返回','img':require('../../assets/img/back.png'),'center':'基本资料'}
    }
  },
  components:{
  	'my-head':head
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.information
	padding-top pxtorem(100px)
	width pxtorem(750px)
	height pxtorem(1234px)
	background-color #f2f2f2
	ul
		width pxtorem(750px)
		li
			width pxtorem(730px)
			height pxtorem(100px)
			padding-right pxtorem(20px)
			border-bottom pxtorem(1px) solid gray
			background-color white
			line-height pxtorem(100px)
			font-size pxtorem(33px)
			text-indent pxtorem(30px)
			color #999
			overflow hidden
			img
				float right
				width pxtorem(25px)
				margin-top pxtorem(25px)
			span
				float right
				margin-right pxtorem(20px)
		li:nth-of-type(1)
			height pxtorem(200px)
			line-height pxtorem(200px)
			img
				margin-top pxtorem(75px)
			div
				float right
				width pxtorem(150px)
				height pxtorem(150px)
				border-radius pxtorem(75px)
				background url(../../assets/img/beauty.png) no-repeat pxtorem(-50px) pxtorem(-30px)
				background-size pxtorem(250px)
				margin pxtorem(25px) pxtorem(20px) 0 0
</style>



