<template>
  <div class="car">
    <my-nav></my-nav>
    <my-head :mes='title'></my-head>
    <div class="goods" v-for='(item,index) in good'>
        <div @click='selectFn(index)' ref='li'><img src="../../assets/img/checked.png" alt=""></div>
        <img :src="item[0].des" alt="">
        <div>
          <p>{{item[0].name}}</p>
          <span>￥{{item[0].price}}</span>
          <button @click='minusFn(index)'>-</button>
          <input type="text" :value='item[1]' ref='num'>
          <button @click='plusFn(index)'>+</button>
        </div>
        <div>
          <img src="../../assets/img/delete.png" alt="" @click='deleteFn(index)'>
        </div>
    </div>
    <div class="cal">
      <div @click='allFn'>
        <div ref='all'><img src="../../assets/img/checked.png" alt=""></div>
        <span>全选</span>
      </div>
      <div>
        <span>合计</span>
        <span>￥{{sum}}</span>
      </div>
      <p @click='goFn'>结&nbsp&nbsp算</p>
    </div>
    <div class="mask" ref='mask' style="display:none">
      <img src="../../assets/img/null.png" alt="">
      <p>购物车空空如也...</p>
      <button @click='buyFn'>去逛逛</button>
    </div>
  </div>
</template>

<script>
import nav from '../nav.vue'
import head from '../head.vue'
import {mapGetters,mapActions} from 'vuex'
import { MessageBox } from 'mint-ui';
export default {
  name: '',
  data () {
    return {
        title:{'center':'购物车','left':'返回','img':require('../../assets/img/back.png')},
        sum:'0.00'
    }
  },
  computed:mapGetters({
    good:'getCartList',
    states:'getStates'
  }),
  components:{
  	'my-nav':nav,
    'my-head':head,
  },
  methods:{
    ...mapActions([
      'setUnpaid',
      'setCartEmpty'
    ]),
    plusFn:function (n) {
      this.$refs.num[n].value++
      this.good[n][1]++
      this.calculate()
    },
    minusFn:function (n) {
      if(this.$refs.num[n].value<=1){
        this.$refs.num[n].value=1
        this.good[n][1]=1
      }else{
        this.$refs.num[n].value--
        this.good[n][1]--
      }
      this.calculate()
    },
    buyFn:function(){
      this.$router.push('/sort')
    },
    selectFn:function(index){
        var allNo=[];
        if(event.currentTarget.style.backgroundColor!='orange'){
            event.currentTarget.style.backgroundColor='orange'
        }else{
            event.currentTarget.style.backgroundColor='rgb(204, 204, 204)'
        }
        for(var i=0;i<this.good.length;i++){
            if(this.$refs.li[i].style.backgroundColor=='orange'){
              allNo.push(1)
            }
        }
        if(allNo.length==this.good.length){
            this.$refs.all.style.backgroundColor='orange'
        }else{
            this.$refs.all.style.backgroundColor='rgb(204, 204, 204)'
        }
        this.calculate()
    },
    calculate:function () {
      var sum=0;
      for(var i=0;i<this.good.length;i++){
          if(this.$refs.li[i].style.backgroundColor=='orange'){
              sum+=parseFloat(this.$refs.num[i].value)*this.good[i][0].price
          }
      }
      this.sum=sum.toFixed(2)
      console.log(this.sum)
    },
    allFn:function(){
        if(this.$refs.all.style.backgroundColor!='orange'){
            this.$refs.all.style.backgroundColor='orange'
            for(var i=0;i<this.good.length;i++){
              this.$refs.li[i].style.backgroundColor='orange'
            }
        }else{
            this.$refs.all.style.backgroundColor='rgb(204, 204, 204)'
            for(var i=0;i<this.good.length;i++){
              this.$refs.li[i].style.backgroundColor='rgb(204, 204, 204)'
            }
        }
        this.calculate()
    },
    deleteFn:function(index){
      MessageBox.confirm('删除商品无法恢复').then(action => {
          this.good.splice(index,1)
          this.calculate()
          if(this.good.length==0){
            this.$refs.mask.style.display='block';
          }
      });
    },
    goFn:function(){
      var temp=[];
      for(var i=0;i<this.good.length;i++){
          if(this.$refs.li[i].style.backgroundColor=='orange'){
              temp.push(this.good[i])
          }
      }      
      if(temp.length==0){
          MessageBox.alert('选择商品不能为空！')
      }else{
        var n=this.sum      
        this.setUnpaid([n,temp])
        this.setCartEmpty()
        this.$router.push('/order')
      }
    }
  },
  mounted:function(){
    if(this.good.length==0){
      this.$refs.mask.style.display='block';
    }
    if(this.states==''){
      this.$router.push('/log')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../../assets/common.styl'
.car
  padding-top pxtorem(100px)
  .goods
    height pxtorem(200px)
    overflow hidden
    border-bottom 1px solid gray
    >div:nth-of-type(1)
      float left
      width pxtorem(40px)
      height pxtorem(40px)
      background-color rgb(204, 204, 204)
      border-radius 50%
      text-align center
      line-height pxtorem(40px)
      margin pxtorem(70px) pxtorem(10px)
      >img
        width pxtorem(30px)
    >img:nth-of-type(1)
      width pxtorem(150px)
      height pxtorem(150px)
      margin pxtorem(25px) pxtorem(5px)
      display block
      float left
    >div:nth-of-type(2)
      width pxtorem(450px)
      height pxtorem(180px)
      float left
      padding-top pxtorem(20px)
      overflow hidden
      >p
        font-size pxtorem(30px)
        margin-bottom pxtorem(30px)
        height pxtorem(60px)
      >span
        font-size pxtorem(45px)
        color #FF7100
        float left
        margin-right pxtorem(160px)
        width pxtorem(150px)
      >button
        width pxtorem(40px)
        height pxtorem(40px)
        font-size pxtorem(30px)
        float left
      >input[type=text]
        width pxtorem(50px)
        height pxtorem(40px)
        text-align center
        border 1px solid #ccc
        box-sizing border-box
        float left
    >div:nth-of-type(3)
      width pxtorem(80px)
      height pxtorem(200px)
      background-color #f2f2f2
      box-sizing border-box
      border-left 1px solid #ccc
      float left
      line-height pxtorem(200px)
      text-align center
      >img
        width pxtorem(50px)
  .cal
    width pxtorem(750px)
    height pxtorem(100px)
    position fixed
    bottom pxtorem(120px)
    left 0
    overflow hidden
    border-top 1px solid #ccc
    div:nth-of-type(1)
      float left
      width pxtorem(150px)
      height pxtorem(100px)
      font-size pxtorem(30px)
      color #292929
      line-height pxtorem(100px)
      div
        width pxtorem(40px)
        height pxtorem(40px)
        background-color #ccc
        border-radius 50%
        text-align center
        line-height pxtorem(40px)
        margin pxtorem(30px) pxtorem(20px)
        img
          width pxtorem(30px)
    div:nth-of-type(2)
      float left
      width pxtorem(250px)
      height pxtorem(100px)
      line-height pxtorem(100px)
      margin-left pxtorem(100px)
      span:nth-of-type(1)
        font-size pxtorem(30px)
      span:nth-of-type(2)
        font-size pxtorem(35px)
        color #FF7100
    p
      width pxtorem(250px)
      height pxtorem(100px)
      background-color #FF7100
      float left
      text-align center
      line-height pxtorem(100px)
      color white
      font-size pxtorem(35px)
  .mask
    position fixed
    width pxtorem(750px)
    height pxtorem(1013px)
    left 0
    top pxtorem(100px)
    background-color #f2f2f2
    text-align center
    >img
      width pxtorem(250px)
      height pxtorem(242px)
      margin-top pxtorem(200px)
    >p
      font-size pxtorem(30px)
      color gray
    >button
      width pxtorem(150px)
      height pxtorem(40px)
      background-color #ff7100
      border none
      border-radius pxtorem(10px)
      color #f2f2f2
      font-size pxtorem(23px)
      opacity 0.8
      margin-top pxtorem(30px)
</style>



