<template>
  <div class="head">
        <img :src="mes.img" alt="" >
        <span class='left' @click='backFn'>{{mes.left}}</span>    
        <span class='center' >{{mes.center}}</span>   
  </div>
</template>

<script>

export default {
  props:['mes'],
  name: '',
  data () {
    return {
      
    }
  },
  methods:{
    backFn:function(){
      this.$router.go(-1);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='stylus' scoped>
@import '../assets/common.styl'
.head
  width pxtorem(750px)
  height pxtorem(100px)
  background-color #292929
  position fixed
  top 0
  left 0
  z-index 999
  .left
    display block
    float left
    height pxtorem(100px)
    color white
    line-height pxtorem(100px)
    font-size pxtorem(35px)
    font-family '宋体'
  img
    height pxtorem(35px)
    margin pxtorem(30px) pxtorem(10px)
    display block
    float left
  .center
    position absolute
    width pxtorem(400px)
    display block
    height pxtorem(100px)
    line-height pxtorem(100px)
    left pxtorem(175px)
    top 0
    text-align center
    color white
    font-size pxtorem(40px)
    font-family '宋体'
</style>



